import hljs from 'highlight.js/lib/core';
import bash from 'highlight.js/lib/languages/bash';
import ruby from 'highlight.js/lib/languages/ruby';
import python from 'highlight.js/lib/languages/python';
import php from 'highlight.js/lib/languages/php';
import java from 'highlight.js/lib/languages/java';
import javascript from 'highlight.js/lib/languages/javascript';
import go from 'highlight.js/lib/languages/go';
import swift from 'highlight.js/lib/languages/swift';
import c from 'highlight.js/lib/languages/c';
import csharp from 'highlight.js/lib/languages/csharp';

import React from 'react';
import classNames from 'classnames';
import { getHighlightClassForCodeLanguage } from 'utils/CodeViewerLanguageMapper';

hljs.registerLanguage('bash', bash);
hljs.registerLanguage('ruby', ruby);
hljs.registerLanguage('python', python);
hljs.registerLanguage('php', php);
hljs.registerLanguage('java', java);
hljs.registerLanguage('javascript', javascript);
hljs.registerLanguage('go', go);
hljs.registerLanguage('swift', swift);
hljs.registerLanguage('c', c);
hljs.registerLanguage('csharp', csharp);

const CodeViewerImpl = ({ children = '', codeLanguage = '', className, highlight = true, ...others }) => {
  let selected = getHighlightClassForCodeLanguage(codeLanguage);

  let content = children;
  let highlightClass;
  if (highlight) {
    content = hljs.highlightAuto(children, selected ? [selected] : undefined).value;
    highlightClass = selected.toLowerCase();
    let domCount = (content.match(/hljs/g) || []).length;

    if (domCount >= 1500) {
      highlight = false;
    }
  }
  return (
    <pre>
      {highlight ? (
        <code
          data-testid="highlight"
          className={classNames(className, { [highlightClass]: selected }, 'hljs')}
          dangerouslySetInnerHTML={{ __html: content }}
          tabIndex={0}
          {...others}
        ></code>
      ) : (
        <code data-testid="highlight" className={classNames(className)} tabIndex={0} {...others}>
          {children}
        </code>
      )}
    </pre>
  );
};

export default CodeViewerImpl;
